// import instance from '../../axios'
import instance from '../../axios'

export function getFilters(id) {
    return instance.get('/houses/condition',{
        params: {
            id
        }
    })
}
// 
export function getHouseList(cityId,filters,start,end) {
    return instance.get('/houses',{
        params: {
            cityId,
            // 
            ...filters,
            // 
            start,
            end
        }
    })
}