// 
import instance from '../../axios'

// 
export function getSwiper() {
    return instance.get('/home/swiper')
}

// 
export function getGroup(area) {
    return instance.get('/home/groups',{
        params: {
            area
        }
    })
}
// 
export function getNews(area) {
    return instance.get('/home/news',{
        params: {
            area
        }
    })
}