import React from 'react'
import { getCityList, getCityHot } from '../../utils/api/city'
import { getCurCity, CRE_CITY, setSession } from '../../utils/index'
import { List, AutoSizer } from 'react-virtualized'
import './index.scss'
import { NavBar, Icon, Toast } from 'antd-mobile'
class CityList extends React.Component {
    state = {
        cityList: {},
        cityIndex: [],
        // ,
        activeIndex: 0
    }
    componentDidMount() {
        this.getCityList()
    }
    // 
    getCityList = async () => {
        const { status, data } = await getCityList()
        console.log(status,data)
        if (status === 200) {
            let { cityList, cityIndex } = this.formmatData(data)
            // 
            let { status: st, data: da } = await getCityHot()
            if (st === 200) {
                cityIndex.unshift('hot')
                cityList['hot'] = da
            }
            const res = await getCurCity()
            cityList['#'] = [res]
            cityIndex.unshift('#')
            this.setState({
                cityList,
                cityIndex
            })

        }
    }
    // 
    // 
    formmatData = (data) => {
        let cityList = {}, cityIndex;
        data.forEach((item) => {
            // 
            let first = item.short.slice(0, 1);
            // 
            if (!(first in cityList)) {
                cityList[first] = [item]
            } else {
                cityList[first].push(item)
            }
        })
        cityIndex = Object.keys(cityList).sort()
        console.log('formated:', cityList, cityIndex);
        return {
            cityIndex,
            cityList
        }
    }

    // title
    // 
    formmatLetter(letter, first) {
        switch (letter) {
            case 'hot':
                return first ? '' : '';
            case '#':
                return first ? '' : '';
            default:
                return letter.toUpperCase();
        }
    }

    // 
    changeCity = (city) => {
        const hasData = ['', '', '', ''];
        if (hasData.includes(city.label)) {
            setSession(CRE_CITY, JSON.stringify(city));
            this.props.history.goBack()
            // this.props.history.push('/')
        } else {
            Toast.info('', 2)
        }
    }


    // 
    // 
    rowRenderer = ({
        key, // Unique key within array of rows
        index, // Index of row within collection
        isScrolling, // The List is currently being scrolled
        isVisible, // This row is visible within the List (eg it is not an overscanned row)
        style // Style object to be applied to row (to position it)
    }) => {
        // 
        const { cityIndex, cityList } = this.state
        // 
        const letter = cityIndex[index]
        // 
        let item = cityList[letter]
        return (
            <div key={key} style={style} className="city-item">
                <div className="title">{this.formmatLetter(letter)}</div>
                {
                    item.map((item) => <div  className="name" key={item.value} onClick={() => {
                        this.changeCity(item)
                    }}>{item.label}</div>)
                }
            </div>
        )
    }


    // 
    // 
    getRowheight = ({ index }) => {
        const { cityIndex, cityList } = this.state;
        let letter = cityIndex[index];
        // title+*
        return 36 + 50 * cityList[letter].length
    }


    // 
    renderCityIndex = () => {
        const { cityIndex,activeIndex } = this.state;
        return cityIndex.map((item, index) => {
            return (
                <li
                    key={item}
                    className="city-index-item"
                    onClick={() => {
                        // 
                        this.refList.scrollToRow(index)
                    }}
                >
                    <span className={activeIndex === index ? 'index-active' : ''}>
                        {this.formmatLetter(item, true)}
                    </span>
                </li>
            )
        })
    }

    // ()
    onRowsRendered = ({ startIndex }) => {
        if (this.state.activeIndex !== startIndex) {
            // console.log(startIndex);
            this.setState({
                activeIndex: startIndex
            })
        }
    }

    render() {
        return (
            <div className="cityList">
                {/*  */}
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}
                >
                    
                </NavBar>
                {/*  */}
                <AutoSizer>
                    {({ height, width }) => (
                        <List
                            ref={(e) => { this.refList = e }}
                            scrollToAlignment="start"
                            onRowsRendered={this.onRowsRendered}
                            height={height}
                            rowCount={this.state.cityIndex.length}
                            rowHeight={this.getRowheight}
                            rowRenderer={this.rowRenderer}
                            width={width}
                        />
                    )}
                </AutoSizer>
                {/*  */}
                <ul className="city-index">
                    {this.renderCityIndex()}
                </ul>
            </div>
        )
    }
}

export default CityList